import { BoomLettersBonus } from "./BoomLettersBonus";

export class BigBoomLettersBonus extends BoomLettersBonus {
    private static all = [
        "YOU ARE JUST GREAT".split(""),
        "YOU ARE GREAT".split(""),
        "SUPER PUPER BOOM".split(""),
        "WISH YOU LUCK, DUDE!".split("")
    ];
    protected getLetters(): string[] {
        return BigBoomLettersBonus.all[Math.floor(Math.random()*BigBoomLettersBonus.all.length)];        
    }
}