import gsap from "gsap";
import { Container, NineSlicePlane, Sprite, Texture, Text } from "pixi.js";
import { gameSettings } from "../../../core/BaseGame";
import { Button } from "../../../core/ui/Button";

export class PausePopup extends Container {
    private callback: (exit: boolean) => void;
    constructor() {
        super();
        const back = Sprite.from(Texture.WHITE);
        back.anchor.set(0.5, 0.5);
        back.width = gameSettings.originalWidth;
        back.height = gameSettings.originalHeight;
        back.tint = 0x000000;
        back.alpha = 0.6;
        this.addChild(back);

        const popupBack = new NineSlicePlane(Texture.from("top_back.png"), 46, 50, 46, 50);
        popupBack.width = 500;
        popupBack.height = 360;
        popupBack.pivot.set(250, 180);
        this.addChild(popupBack);

        const title = new Text("Exit?", {fontFamily: "FontsFree-Net-AmericanTypewriterBold", fontSize: 70, fill: 0x5f40e3, align: "center", fontWeight: "bold"})
        title.anchor.set(0.5, 0.5);
        title.position.y = -120;
        this.addChild(title);

        const okBut = new Button("ok_button.png", "ok_button.png");
        okBut.on("pointerup", (e)=> {
            e.stopPropagation();
            e.stopped = true;
            this.hide(true);
        });
        okBut.position.set(-110, 110);
        okBut.scale.set(0.8, 0.8);
        this.addChild(okBut);

        const xBut = new Button("x_button.png", "x_button.png");
        xBut.on("pointerup", (e)=> {
            e.stopPropagation();
            e.stopped = true;
            this.hide(false);
        });
        xBut.position.set(110, 110);
        xBut.scale.set(0.8, 0.8);
        this.addChild(xBut);
        
        this.visible = false;
    }
    public show(callback: (exit: boolean)=>void) {
        this.callback = callback;
        this.alpha = 1;
        this.visible = true;
        //gsap.to(this, {alpha: 1, duration: 0.4});
    }
    private hide(exit: boolean) {
        this.callback(exit);
        this.callback = undefined;
        gsap.to(this, {alpha: 0, duration: 0.4, onComplete: ()=> {
            this.visible = false;
        }});
    }
}