import { Container, Point, Sprite, Texture } from "pixi.js";
import { GameViewState } from "../GameViewState";

export class MousePreview extends Container {
    private part0: Sprite;
    private part1: Sprite;
    constructor() {
        super();
        this.part0 = Sprite.from(Texture.from("direction.png"));
        this.part0.anchor.set(0.5, 1);
        this.addChild(this.part0);
        this.part0.height = 1200;

        this.part1 = Sprite.from(Texture.from("direction.png"));
        this.part1.anchor.set(0.5, 1);
        this.addChild(this.part1);
        this.part1.height = 700;
    }

    public draw(p: Point) {
        const centerY = GameViewState.BottomPoint;
        const len = 1200;
        const boundX = 315;
        
        const angle= Math.atan2((p.y-centerY), p.x);
        this.part0.rotation = angle + Math.PI/2;
        let xx = len*Math.cos(angle);
        let yy = centerY+len*Math.sin(angle);
        let d = len;
        if (xx < -boundX || xx > boundX) {
            d = Math.abs(xx)/xx * boundX / Math.cos(angle);
            xx = d*Math.cos(angle);
            yy = d*Math.sin(angle);
            this.part1.visible = true;
            this.part1.position.set(xx, yy);
            this.part1.rotation = -angle - Math.PI/2;
        } else {
            this.part1.visible = false;
        }
        this.part0.height = d;
    }
}