import gsap from "gsap";
import { Container, NineSlicePlane, Sprite, Text, Texture } from "pixi.js";
import { EIAPBonus } from "../../GameViewState";

export class WheelBox extends Container {
    private iconL0: Sprite;
    private iconR0: Sprite;
    private iconL1: Sprite;
    private iconR1: Sprite;
    private static allTextures: Map<EIAPBonus, Texture>;
    constructor() {
        super();
        const back = new NineSlicePlane(Texture.from("top_back.png"), 46, 50, 46, 50);
        back.tint = 0x000000;
        back.width = 370;
        back.height = 250;
        back.pivot.set(370*0.5, 200*0.5);
        this.addChild(back);
        back.y = -50;

        const t = new Text("level powerups:",{fontFamily: "FontsFree-Net-AmericanTypewriterBold", fontSize: 35, fill: 0xFFFFFF, align: "center", fontWeight: "bold"});
        t.anchor.set(0.5,0.5);
        t.position.set(0, -115);
        this.addChild(t);

        const back1 = new NineSlicePlane(Texture.from("top_back.png"), 46, 50, 46, 50);
        back1.pivot.set(80, 80);
        back1.width = 160;
        back1.height = 160;
        this.addChild(back1);

        const back2 = new NineSlicePlane(Texture.from("top_back.png"), 46, 50, 46, 50);
        back2.pivot.set(80, 80);
        back2.width = 160;
        back2.height = 160;
        this.addChild(back2);

        back1.position.set(-85, 0);
        back2.position.set(85, 0);

        const slot1 = new Container();        
        const slot2 = new Container();        
        this.addChild(slot1);
        this.addChild(slot2);
        slot1.position.x = -85;
        slot2.position.x = 85;

        const mask1 = Sprite.from(Texture.WHITE);
        mask1.anchor.set(0, 0.5);
        mask1.width = 140;
        mask1.height = 140;
        this.addChild(mask1);

        const mask2 = Sprite.from(Texture.WHITE);
        mask2.anchor.set(0, 0.5);
        mask2.width = 140;
        mask2.height = 140;
        this.addChild(mask2);

        mask1.position.x = -145;
        mask2.position.x = 5;
        slot1.mask = mask1; 
        slot2.mask = mask2;

        this.iconL0 = Sprite.from(WheelBox.randomTexture);
        this.iconL0.anchor.set(0.5, 0.5);
        this.iconL0.scale.set(0.5, 0.5);
        slot1.addChild(this.iconL0);

        this.iconR0 = Sprite.from(WheelBox.randomTexture);
        this.iconR0.anchor.set(0.5, 0.5);
        this.iconR0.scale.set(0.5, 0.5);
        slot2.addChild(this.iconR0);

        this.iconL1 = Sprite.from(WheelBox.randomTexture);
        this.iconL1.anchor.set(0.5, 0.5);
        this.iconL1.scale.set(0.5, 0.5);
        slot1.addChild(this.iconL1);
        this.iconL1.y = 140;
        
        this.iconR1 = Sprite.from(WheelBox.randomTexture);
        this.iconR1.anchor.set(0.5, 0.5);
        this.iconR1.scale.set(0.5, 0.5);
        slot2.addChild(this.iconR1);
        this.iconR1.y = 140;
    }
    public start(bonuses: EIAPBonus[]) {
        gsap.to({a:0}, {a: 1, duration: 2, onUpdate: ()=> {
            if (this.iconL0.y >= 140) {
                this.iconL0.texture = WheelBox.randomTexture;
                this.iconR0.texture = WheelBox.randomTexture;
                let temp = this.iconL0;
                this.iconL0 = this.iconL1;
                this.iconL1 = temp;

                temp = this.iconR0;
                this.iconR0 = this.iconR1;
                this.iconR1 = temp;

                this.iconL0.y = 0;
                this.iconR0.y = 0;
            }
            this.iconL0.y += 10;
            this.iconR0.y += 10;
            this.iconL1.y = this.iconL0.y - 140;
            this.iconR1.y = this.iconR0.y - 140;
        }, onComplete: ()=> {
            this.iconL0.y = 0;
            this.iconR0.y = 0;
            this.iconR1.y = 140;
            this.iconL1.y = 140;
            this.iconL0.texture = WheelBox.getTexture(bonuses[0]);
            this.iconR0.texture = WheelBox.getTexture(bonuses[1]);
        }});
    }
    public static prepareTextures() {
        this.allTextures = new Map<EIAPBonus, Texture>();
        this.allTextures.set(EIAPBonus.RocketUp, Texture.from("powerup_rocket.png"));
        this.allTextures.set(EIAPBonus.Kill, Texture.from("powerup_bomb.png"));
        this.allTextures.set(EIAPBonus.Freeze, Texture.from("powerup_freeze.png"));
    }
    public static getTexture(k: EIAPBonus) {
        return this.allTextures.get(k);
    }
    private static get randomTexture() {
        const keys = Array.from(this.allTextures.keys());
        const k = keys[Math.floor(Math.random() * keys.length)];
        return this.getTexture(k);
    }
}