import gsap, { Expo } from "gsap";
import { Container, Sprite, Text, Texture } from "pixi.js";
import { gameSettings } from "../../../core/BaseGame";
import { EIAPBonus, ILevelData } from "../GameViewState";
import { WheelBox } from "./levelstart/WheelBox";

export class LevelStart extends Container {
    private texts: Text[];
    private usingTexts: Text[];
    private back: Sprite;
    private wheelbox: WheelBox;
    private tween: gsap.core.Tween;
    constructor() {
        super();
        this.back = Sprite.from(Texture.WHITE);
        this.back.anchor.set(0.5, 0.5);
        this.back.width = gameSettings.originalWidth;
        this.back.height = gameSettings.originalHeight;
        //this.back.tint = 0x000000;
        this.back.alpha = 0;
        this.addChild(this.back);
        
        this.texts = [];
        this.usingTexts = [];
        for (let i = 0; i < 4; i++) {
            const t = this.getNewText();
            this.texts.push(t);
        }

        this.wheelbox = new WheelBox();
        this.addChild(this.wheelbox);

        this.visible = false;
    }
    public async show (data: ILevelData, bonuses?: EIAPBonus[]) {
        return new Promise<void>((resolve)=> {
            this.visible = true;
            this.back.alpha = 0;
            this.wheelbox.position.y = 1000;
            const delay = (bonuses && bonuses.length > 0) ? 5 : 3;
            gsap.to(this.back, {alpha: 0.7, duration: 0.5});
            const words = data.name.split(" ");
            let yy = -(words.length * 70) / 2 - 100;
            for (let i = 0; i < words.length; i++) {
                const w = words[i];
                let t: Text; 
                if (this.texts.length > 0) {
                    t = this.texts.pop();
                } else {
                    t = this.getNewText();
                }
                t.alpha = 1;
                t.visible = true;
                t.text = w;
                this.addChild(t);
                this.usingTexts.push(t);
    
                t.y = yy;
                t.x = i%2 ? (-500-t.width/2) : (500+t.width/2);
                gsap.to(t, {x: 0, duration: 1, ease: Expo.easeOut});
                gsap.to(t, {alpha: 0, delay: delay - 1, duration: 0.5, onComplete: ()=> {
                    this.texts.push(t);
                }});
                yy += 70;
            }
            gsap.to(this.back, {alpha: 0, delay: delay - 1, duration: 0.5});
            
            if (bonuses && bonuses.length > 0) {
                this.wheelbox.visible = true;
                this.wheelbox.alpha = 1;
                
                gsap.to(this.wheelbox, {y: yy + 120, delay: 1, duration: 0.5, ease: Expo.easeOut, onComplete: ()=> {
                    this.wheelbox.start(bonuses);
                }});
                gsap.to(this.wheelbox, {alpha: 0, delay: delay - 1, duration: 0.5});
            }
            this.tween = gsap.delayedCall(delay, ()=> {
                this.usingTexts = [];
                this.visible = false;
                this.tween = undefined;
                resolve();
            })
        });
    }
    public clear() {
        if (this.tween) {
            this.tween.kill();
            this.tween = undefined;
        }
        gsap.killTweensOf(this.wheelbox);
        gsap.killTweensOf(this.back);
        for (const t of this.usingTexts) {
            gsap.killTweensOf(t);
            this.removeChild(t);
        }
        this.visible = false;
    }
    private getNewText() {
        const t = new Text("",{fontFamily: "FontsFree-Net-AmericanTypewriterBold", fontSize: 70, fill: 0x000000, align: "center", fontWeight: "bold"});
        t.anchor.set(0.5,0.5);
        return t;
    }
}