import gsap from "gsap";
import { AnimatedSprite, Container, Graphics, NineSlicePlane, Sprite, Texture } from "pixi.js";

export class BoomMeter extends Container {
    private bar: Sprite;
    private max: number;
    private current: number;
    private initialWidth: number = 170;
    private barMask: Sprite;
    private barBorder: Sprite;
    private barShine: AnimatedSprite;
    constructor() {
        super();
        const back = Sprite.from(Texture.from("battery_empty.png"));
        this.addChild(back);

        this.bar = Sprite.from(Texture.from("battery_full.png"));
        this.addChild(this.bar);

        this.barMask = new Sprite(Texture.WHITE);
        this.barMask.width = 0;
        this.barMask.height = this.bar.height;
        this.barMask.x = 27;
        this.addChild(this.barMask);

        this.barBorder = Sprite.from(Texture.from("battery_border.png"));
        this.barBorder.anchor.set(0.5, 0.5);
        this.barBorder.y = this.bar.height * 0.5;
        this.addChild(this.barBorder);
        this.barBorder.visible = false;

        this.barShine = new AnimatedSprite([Texture.from("battery_charging0.png"), Texture.from("battery_charging1.png"), Texture.from("battery_charging2.png"), Texture.from("battery_charging1.png")]);
        this.addChild(this.barShine);
        this.barShine.animationSpeed = 0.3;
        this.barShine.visible = false;        

        this.bar.mask = this.barMask;

        this.current = 0;
        this.max = 10;
    }
    public set maximum(m: number) {
        this.max = m;
    }
    public reset(noAnimation?: boolean) {
        gsap.killTweensOf(this.barMask);
        this.current = 0;
        if (noAnimation) {
            this.barMask.width = 0;
        } else {
            gsap.to(this.barMask, {width: 0, duration: 0.1});
        }
       
        this.barBorder.visible = false;
    }
    public update(amount: number): boolean {
        this.current += amount;
        this.current = Math.min(this.current, this.max);
        this.barShine.visible = true;
        this.barShine.gotoAndPlay(0);
        const wasEnd = this.current >= this.max;
        const p = this.current / this.max;
        this.barBorder.visible = p > 0.05 && p < 0.9;
        gsap.killTweensOf(this.barMask);
        gsap.to(this.barMask, {width: this.initialWidth * p, duration: 0.2});
        gsap.to(this.barBorder, {x: this.barMask.x + this.initialWidth * p, duration: 0.2, onComplete: ()=> {
            this.barShine.visible = false;
            this.barShine.stop();
        }});
        if (wasEnd) {
            this.barShine.visible = false;
            this.barShine.stop();
            this.current = 0;
            gsap.to(this.barMask, {width: 0, delay: 0.2, duration: 0.2});
            gsap.to(this.barBorder, {x: this.barMask.x, duration: 0.2, onUpdate: ()=> {
                this.barBorder.visible = p > 0.05 && p < 0.9;
            }});
        }
        return wasEnd;
    }
}